.option {
    border-bottom: 1px solid lighten($light-blue, 10%);
    display: flex;
    justify-content: space-between;
    padding: $m-size $m-size;
}

.option-role {
    color: white;
    font-weight: 500;
    font-size: 2rem;
    display: flex;
    justify-content: space-between;
    padding: $m-size;
    margin: $s-size;
}

.option__text {
    color: white;
    font-weight: 500;
    font-size: 2rem;
    margin: 0;
}

